import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class MessageService {
  messages: string[] = [];
  public messaged:boolean=false;

  add(message: string) {
    this.messages.push(message);
    this.messaged=true;
  }

  clear() {
    this.messages = [];
    this.messaged=false;
  }
}