import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';


import { HttpClient } from '@angular/common/http';
import { IMovie, Movie} from '../../movies';
import { Title } from "@angular/platform-browser";

import { MessageService } from '../../admin.module/message.service';
import { PanelService } from './movie-panel.service';


@Component({
  selector: 'app-movie-panel',
  templateUrl: './movie-panel.component.html',
  styleUrls: ['./movie-panel.component.css']
})
export class MoviePanelComponent implements OnInit {

  GETALL_url = 'http://localhost:8080/movie/getAll';

  movies: IMovie[];
  movie: Movie=new Movie();

  error:boolean=false;
  display: boolean = false;
  loaded: boolean = false;
  expanded: boolean = false;
  expandedId: number=0;

  selected: any;
  editing: boolean = false;

  displaingElement: boolean = false;
  addingElement: boolean = false;
  removingElement: boolean = false;

  constructor(
    private http: HttpClient,
    private movieService: PanelService,
    private titleService:Title,
    public messageService: MessageService,
  ){
    this.titleService.setTitle("Movies")
  }

  displayDisplaingElement(){this.displaingElement=!this.displaingElement;}
  displayAddingElement(){this.addingElement=!this.addingElement;}
  displayRemovingElement(){this.removingElement=!this.removingElement;}

  ifError(){
    this.scrollToBottom();
    if(this.messageService.messages){
      this.error=true;  
    } 
  }

  async submit(movie){
    this.movieService.postMovie(movie);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }
  
  id: number;

  async delete(id){
    this.movieService.deleteMovie(id);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

   async deleteRow(id){
    this.movieService.deleteMovie(id);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

  async modify(movie){
    console.log(JSON.stringify(movie));
    this.movieService.modifyMovie(movie);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

   edit(){
    this.editing = !this.editing;
   }

  expandRow(id){
    if(this.expandedId==0){
      this.expandedId = id;
      this.expanded = !this.expanded;
      return;
    } 
    if(this.expandedId==id){
      this.expanded = !this.expanded;
      this.expandedId = 0;
      return;
    }
    this.expandedId = id;
  }

  onDisplay(){
    this.display=!this.display;
  }

  onLoad(){
    this.movieService.getMovies().subscribe({
      next: movies =>{ 
        this.movies=movies;
      },
      error: err =>this.errorMessage = err
    });
    this.loaded=true;
  }
  

  private errorMessage="/movie/getAll is unavailable"


  ngOnInit(): void {

  }

  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }

  //###Scroling#############################################################################################
  @ViewChild('scroll') private myScrollContainer: ElementRef;

  ngAfterViewChecked() {        
      this.scrollToBottom();        
  } 

  scrollToBottom(): void {
      try {
          this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
      } catch(err) { }                 
  }
}