import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { ICinema, Cinema} from '../../cinemas';
import { Title } from "@angular/platform-browser";

import { CinemaService } from './cinema-panel-service';
import { catchError, isEmpty } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { logging, error } from 'protractor';

import { MessageService } from '../../admin.module/message.service';

@Component({
  selector: 'app-cinema-panel',
  templateUrl: './cinema-panel.component.html',
  styleUrls: ['./cinema-panel.component.css']
})
export class CinemaPanelComponent implements OnInit {

  

  cinemas: ICinema[];
  cinema: Cinema=new Cinema();

  id: number;

  error:boolean=false;
  display: boolean = false;
  loaded: boolean = false;

  selected: any;
  editing: boolean = false;

  displaingElement: boolean = false;
  addingElement: boolean = false;
  removingElement: boolean = false;

  constructor(
    private http: HttpClient,
    private cinemaService: CinemaService,
    private titleService:Title,
    public messageService: MessageService
    
  ){
    this.titleService.setTitle("cinemas");
  }

  displayDisplaingElement(){this.displaingElement=!this.displaingElement;}
  displayAddingElement(){this.addingElement=!this.addingElement;}
  displayRemovingElement(){this.removingElement=!this.removingElement;}

  ifError(){
    this.scrollToBottom();
    if(this.messageService.messages){
      this.error=true;  
    } 
  }

  async submit(cinema){
    this.cinemaService.postCinema(cinema);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }
  
  async delete(id){
    this.cinemaService.deleteCinema(id);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

  async modify(cinema){
    this.cinemaService.modifyCinema(cinema);
    //--------------------//
    this.ifError();
    this.onDisplay();
    await this.delay(100);
    this.onLoad();
    this.onDisplay();
  }

   edit(){
    this.editing = !this.editing;
   }

  onDisplay(){
    this.display=!this.display;
  }

  onLoad(){
    this.cinemaService.getCinemas()
    .subscribe({
      next: cinemas =>{ 
        this.cinemas=cinemas;
      },      
    });
    this.loaded=true;
  }

  ngOnInit(): void {

  }

  delay(ms: number) {
    return new Promise( resolve => setTimeout(resolve, ms) );
  }

  //###Scroling#############################################################################################
  @ViewChild('scroll') private myScrollContainer: ElementRef;

  ngAfterViewChecked() {        
      this.scrollToBottom();        
  } 

  scrollToBottom(): void {
      try {
          this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
      } catch(err) { }                 
  }



}