import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError, from } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';

import { MessageService } from '../../admin.module/message.service';
import { ICinema, Cinema } from '../../cinemas';


@Injectable({
  providedIn: 'root',
})
export class CinemaService {
  private moviesUrl='http://localhost:8080/cinema/getAll';
  private movieUrl='http://localhost:8080/cinema/get/';
  private MOD_url = 'http://localhost:8080/cinema/update';
  private ADD_url = 'http://localhost:8080/cinema/add';
  private DEL_url = 'http://localhost:8080/cinema/delete/';

  date = new Date();

  constructor(
    private http: HttpClient,
    private messageService: MessageService
    ) {}

    getCinemas(): Observable<ICinema[]> {
        return  this.http.get<ICinema[]>(this.moviesUrl)
        .pipe(
            catchError(this.handleError)  
            );      
    }

    getCinema(id: number): Observable<ICinema> {
      return  this.http.get<ICinema>(this.movieUrl+id)
      .pipe(
          catchError(this.handleError)  
          );      
    }

    postCinema(cinema){
      return this.http.post(this.ADD_url, cinema).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      );
    }

    deleteCinema(id){
      return this.http.delete(this.DEL_url+id).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      );
    }

    modifyCinema(cinema){
      return this.http.put(this.MOD_url, cinema).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      )
    }

    errorMessage="An Error occured, try again";

    public handleError(err: HttpErrorResponse) {
      let errorMessage = '';
      if (err.error instanceof ErrorEvent) {
        errorMessage = `CPS An error occurred: ${err.error.message}`;
        this.messageService.add(`CPS An error occurred: ${err.error.message}`);
      } else {
        errorMessage = `CPS Server returned code: ${err.status}, error message is: ${err.message}`;
        this.messageService.add(`CPS Server returned code: ${err.status}, error message is: ${err.message}`);
      }
      this.errorMessage=errorMessage;
      console.error(errorMessage);
      return throwError(errorMessage);
    }

}        