import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule} from '@angular/router' ;
import { FormsModule } from '@angular/forms';

import { MoviePanelComponent } from './movie-panel/movie-panel.component';
import { CinemaPanelComponent } from './cinema-panel/cinema-panel.component';

@NgModule({
  declarations: [
    MoviePanelComponent,
    CinemaPanelComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild([
      {path: 'panel/movie', component: MoviePanelComponent},
      {path: 'admin/movie', component: MoviePanelComponent},
      {path: 'panel/cinema', component: CinemaPanelComponent},
      {path: 'admin/cinema', component: CinemaPanelComponent},
    ])
  ]
})
export class AdminModule{ }
