import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule} from '@angular/router' ;
import { HttpClientModule} from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import {MatInputModule} from '@angular/material/input';
import {MatSelectModule} from '@angular/material/select';

import { AppComponent } from './app.component';
import { WelcomeComponent } from './welcome/welcome.component';
import { MovieDetailComponent } from './client.module/movies/movie-detail.component';
import { CinemaComponent } from './client.module/cinema/cinema.component';

import { ClientModule } from './client.module/client.module';
import { AdminModule } from './admin.module/admin.module';

@NgModule({
  declarations: [
    AppComponent,
    WelcomeComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    ClientModule,
    AdminModule,
    RouterModule.forRoot([
      {path: 'kino', component: CinemaComponent},
      {path: 'movie/:id', component: MovieDetailComponent},
      {path: '', redirectTo: 'welcome', pathMatch: 'full'},
      {path: '**', redirectTo: 'welcome', pathMatch: 'full'}
    ]),
    FormsModule,
    MatSelectModule,
    MatInputModule,
    BrowserAnimationsModule,
  ],
  
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }
