import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';

import { MessageService } from '../../admin.module/message.service';
import { IMovie } from '../../movies';


@Injectable({
  providedIn: 'root',
})
export class PanelService {
  private moviesUrl='http://localhost:8080/movie/getAll';
  private movieUrl='http://localhost:8080/movie/get/';
  private ADD_url = 'http://localhost:8080/movie/add';
  private DEL_url = 'http://localhost:8080/movie/delete/';
  private MOD_url = 'http://localhost:8080/movie/update';

  date = new Date();

  constructor(
    private http: HttpClient,
    private messageService: MessageService
    ){}

    getMovies(): Observable<IMovie[]> {
        return  this.http.get<IMovie[]>(this.moviesUrl)
        .pipe(
            //tap(data =>console.log('Wszystko: ' + JSON.stringify(data))),
            catchError(this.handleError)  
            );      
    }

    getMovie(id: number): Observable<IMovie> {
      return  this.http.get<IMovie>(this.movieUrl+id)
      .pipe(
          //tap(data =>console.log('jeden: ' + JSON.stringify(data))),
          catchError(this.handleError)  
          );      
    }

    postMovie(movie){
      return this.http.post(this.ADD_url, movie).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      );
    }

    deleteMovie(id){
      return this.http.delete(this.DEL_url+id).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      );
    }

    modifyMovie(movie){
      return this.http.put(this.MOD_url, movie).subscribe(
        res => console.log('HTTP response', res),
        err => { this.date = new Date();this.messageService.add(" ["+this.date.toLocaleTimeString()+"] "+err.error +" | error code is: "+err.status)},
        () => console.log('HTTP request completed.')
      )
    }
    
    private handleError(err: HttpErrorResponse) {
      let errorMessage = '';
      if (err.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        errorMessage = `An error occurred: ${err.error.message}`;
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong,
        errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
      }
      console.error(errorMessage);
      return throwError(errorMessage);
    }
}

